.side-menu {
    position: fixed;
    display: flex;
    justify-content: flex-end;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    transform: translateX(100%);
    transition: all 1s;
    overflow: hidden;
}

.side-menu-active {
    position: fixed;
    display: flex;
    justify-content: flex-end;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 10;
}

.side-menu__content {
    box-sizing: border-box;
    position: relative;
    height: 100%;
    width: 50%;
    max-width: 26rem;
    background-color: #fff;
    box-shadow: 0 0 0 2px black;
    transform: translateX(100%);
    transition: all 2s;
}

.side-menu__content-active {
    background-color: green;
    box-sizing: border-box;
    position: relative;
    z-index: 12;
    height: 100%;
    width: 30%;
    max-width: 26rem;
    background-color: white;
    /* border-left: 5px solid purple; */
    box-shadow: 0 0 0 2px black;
    transform: translateX(0%);
    transition: all 1s;
    margin: 0;
}

.side-menu__overlay {
    position: absolute;
    display: flex;
    height: 100%;
    width: 100%;
}

.side-menu__overlay-active {
    position: absolute;
    display: block;
    height: 100%;
    width: 100%;
    background-color: black;
    opacity: 0.3;
}

.MenuOptions {
    display: flex;
    flex-direction: column;
    margin-top: 200px;
}

#quitButton {
    position: absolute;
    right: 0;
    margin-top: 35px;
    margin: 45px 40px;
    cursor: pointer;
}

#quitButton:hover, #quitButton:focus {
    opacity: .6;
}

.MenuOption {
    margin: 20px 40px;
    text-decoration: none;
    transition: all .1s ease-in-out;
    font-size: 30px;
}

@media screen 
and (min-width : 481px) {
    .MenuOption:hover, .MenuOption:focus {
        font-size: 40px;
        color: purple;
    }
}

@media screen 
and (max-width : 480px) {
    .MenuOption:hover, .MenuOption:focus {
        color: purple;
    }
    .side-menu__content-active {
        background-color: green;
        box-sizing: border-box;
        position: relative;
        z-index: 12;
        height: 100%;
        width: 75%;
        max-width: 26rem;
        background-color: white;
        /* border-left: 5px solid purple; */
        box-shadow: 0 0 0 2px black;
        transform: translateX(0%);
        transition: all 1s;
        margin: 0;
    }
}